import React, { FC } from "react";
import { useState } from "react";
import api from "../../api";
import { _t } from "../../../utils/Translator";
import { ContentType, HttpVerb, sendHttpRequest } from "../../../utils/httpRequest";
import LazySearchSelect from "../searchselect/LazySearchSelect";
import Error from "../error/Error";

export interface CaseItem {
  name: string;
  id: number;
}

export interface CaseProps {
  selectedCase: CaseItem;
  onCaseSelected: (c: CaseItem) => void;
}

/* global localStorage */

const getConnectionToken = () => {
  return "Bearer " + localStorage.getItem("odooConnectionToken");
};

const Case: FC<CaseProps> = ({ selectedCase, onCaseSelected }) => {
  const [cases, setCases] = useState<CaseItem[]>([]);
  const [error, setError] = useState<string>("");

  // Helper function to get user info loaded
  const searchCases = async (query: string) => {
    if (!query || query.length < 2) return;
    let response = null;
    try {
      const request = sendHttpRequest(
        HttpVerb.POST,
        api.baseURL + api.searchCases,
        ContentType.Json,
        getConnectionToken(),
        { query },
        true
      );
      response = JSON.parse(await request.promise);
      if (response.result.success) {
        setCases(response.result.data.cases);
      } else {
        setError(response.result.error);
      }
    } catch (error) {
      setError(String(error));
    }
    return response;
  };

  const getCaseButton = () => {
    if (selectedCase) {
      const url = `${api.baseURL}/web#id=${selectedCase.id}&model=cases.case&view_type=form`;
      return (
        <a href={url} target="_blank" rel="noreferrer">
          <i className="fa fa-external-link" aria-hidden="true"></i>
        </a>
      );
    }
    return null;
  };

  return (
    <div>
      <div className="page-section-title">
        {_t("Case")}&nbsp;{getCaseButton()}
      </div>
      <Error error={error} setError={setError} />
      <LazySearchSelect
        items={cases}
        selectedItem={selectedCase}
        onItemSelected={onCaseSelected}
        selectText={_t("Select case")}
        searchText={_t("Search case")}
        onSearch={searchCases}
      />
    </div>
  );
};

export default Case;
