import * as React from "react";
import { useState } from "react";
import Login from "./login/Login";
import Main from "./main/Main";
import { saveTranslations } from "../../utils/Translator";
import { ContentType, HttpVerb, sendHttpRequest } from "../../utils/httpRequest";
import api from "../api";

/* global localStorage */

enum Page {
  Main = "Main",
  Login = "Login",
}

const App: React.FC = () => {
  const [page, setPage] = useState<Page>(Page.Main);

  const getTranslations = async () => {
    let response = null;
    try {
      const request = sendHttpRequest(
        HttpVerb.POST,
        api.baseURL + api.getTranslations,
        ContentType.Json,
        localStorage.getItem("odooConnectionToken"),
        true
      );
      response = JSON.parse(await request.promise);
      saveTranslations(response.result);
    } catch (error) {
      console.log("An error occured getting when getting translations from Odoo: " + String(error));
    }
  };

  const connect = (token) => {
    localStorage.setItem("odooConnectionToken", token);
    getTranslations();
    setPage(Page.Main);
  };

  const disconnect = () => {
    localStorage.removeItem("odooConnectionToken");
    localStorage.removeItem("translations");
    localStorage.removeItem("translationsTimestamp");
    setPage(Page.Login);
  };

  const getPage = () => {
    const loginPage = <Login onLogin={connect} />;
    const mainPage = <Main disconnect={disconnect} />;

    switch (page) {
      case Page.Login:
        return loginPage;
      case Page.Main:
        return mainPage;
      default:
        return mainPage;
    }
  };

  return <div>{getPage()}</div>;
};

export default App;
